.PracticeEntry {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
}

.PracticeEntry:not(:first-child) {
  margin-left: 8px;
}

.PracticeEntry:not(:last-child) {
  margin-right: 8px;
}

.PracticeEntry-value {
  padding: 0 8px;
  background-color: #BBDEFB;
  border-radius: 4px;
  margin-right: 8px;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.PracticeEntry-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.PracticeEntry-input {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-right: 8px;
}

.PracticeEntry-save {
  width: 32px;
  background-color: #4CAF50;
  color: #FFF;
}

.PracticeEntry-delete {
  width: 32px;
  background-color: #D50000;
  color: #FFF;
}

@media only screen and (max-width: 600px) {
  .PracticeEntry,
  .PracticeEntry:not(:first-child),
  .PracticeEntry:not(:last-child) {
    margin: 0 0 8px;
    -ms-flex-preferred-size: unset;
        flex-basis: unset;
  }
}


