.Datepicker {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 16px;
}

.Datepicker-left,
.Datepicker-right {
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
  background-color: #E0E0E0;
}

.Datepicker-date {
  -ms-flex-positive: 2;
      flex-grow: 2;
  text-align: center;
  vertical-align: middle;
  margin: 0 16px;
  border-radius: 4px;
  -webkit-box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
          box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
}


